  z-index: 10;
}
.head .logo {
  width: 120px;
  height: 34px;
  margin: 0 auto;
  padding: 3px 20px;
  line-height: 34px;
  color: #ccc;
  font-weight: 700;
}
.list-type {
  position: fixed;
  top: 34px;
  left: 0;
  width: 100%;
  padding: 5px ;
  background: #e1e1e1;
  z-index: 10;
}
.list-type ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  line-height: 2;
  text-align: center;
  background: #f6f6f6;
  border-radius: 5px;
}
.list-type li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #80bd01;
}
.list-type li::before,
.list-type li::after {
  display: inline-block;
  opacity: 0;
  -webkit-transition: opacity 0.2s, -webkit-transform 0.3s;
  transition: opacity 0.2s, -webkit-transform 0.3s;
  transition: transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s, -webkit-transform 0.3s;
}
.list-type li::before {
  margin-right: 5px;
  content: '[';
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}
.list-type li::after {
  margin-left: 5px;
  content: ']';
  -webkit-transform: translateX(-20px);
          transform: translateX(-20px);
}
.list-type li.active::before,
.list-type li.active::after {
  opacity: 1;
  -webkit-transform: translateX(0px);
  transform: translateX(0px);
}
.list {
  overflow: hidden;
  margin-top: 40px;
  border-radius: 5px;
}
.list li {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding: 10px;
  background: #fff;
  border-bottom: 1px solid #f0f0f0;
}
.list li:hover {
  background: #f5f5f5;
}
.list li img {
  float: left;
  width: 48px;
  margin-right: 10px;
}
.list .type {
  display: inline-block;
  padding: 1px 4px;
  border-radius: 3px;
  background-color: #e5e5e5;
  color: #999;
  font-size: 12px;
}
.list .typegood {
  background-color: #80bd01;
  color: #fff;
}
.list .typetop {
  background-color: #ff3838;
  color: #fff;
}
.list .title {
  margin: 0 58px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.list .title a {
  color: #333;
}
.list .title a:visited {
  color: #888;
}
.list .count {
  position: absolute;
  top: 20%;
  right: 3%;
  font-size: 10px;
  color: #b4b4b4;
}
.list .count span {
  color: #80bd01;
}
.list .date {
  position: absolute;
  bottom: 20%;
  right: 3%;
  font-size: 10px;
  color: #777;
}
